'use strict';

/**
* Compute an absolute value of a signed 32-bit integer.
*
* @module @stdlib/math-base-special-labs
*
* @example
* var labs = require( '@stdlib/math-base-special-labs' );
*
* var v = labs( -1|0 );
* // returns 1
*
* v = labs( 2|0 );
* // returns 2
*
* v = labs( 0|0 );
* // returns 0
*/

// MODULES //

var labs = require( './main.js' );


// EXPORTS //

module.exports = labs;
