'use strict';

/**
* Evaluate the ramp function.
*
* @module @stdlib/math-base-special-ramp
*
* @example
* var ramp = require( '@stdlib/math-base-special-ramp' );
*
* var v = ramp( 3.14 );
* // returns 3.14
*
* v = ramp( -3.14 );
* // returns 0.0
*
* v = ramp( NaN );
* // returns NaN
*/

// MODULES //

var ramp = require( './main.js' );


// EXPORTS //

module.exports = ramp;
