'use strict';

/**
* Compute the Bessel function of the second kind of order one.
*
* @module @stdlib/math-base-special-bessely1
*
* @example
* var y1 = require( '@stdlib/math-base-special-bessely1' );
*
* var v = y1( 0.0 );
* // returns -Infinity
*
* v = y1( 1.0 );
* // returns ~-0.781
*
* v = y1( -1.0 );
* // returns NaN
*
* v = y1( Infinity );
* // returns 0.0
*
* v = y1( -Infinity );
* // returns NaN
*
* v = y1( NaN );
* // returns NaN
*/

// MODULES //

var y1 = require( './y1.js' );


// EXPORTS //

module.exports = y1;
